import React from 'react'
import './index.css'
import { Input,Button } from 'antd';
import axios from 'axios'
import {HeartTwoTone} from '@ant-design/icons';
import qs from 'querystring'
const { TextArea } = Input;
export default function Message() {
    const [messageContent,setMessageContent]=React.useState([])
    const [text,setText]=React.useState('')
    const [loveBgc,setloveBgc]=React.useState('')
    const updateData=(callback)=>{
        axios.get('http://aays.top:4000/board/search').then(res=>{
            setMessageContent(res.data.reverse())
            callback()
        },
        err=>{
            console.log('查询失败');
        }
        )
    }
    const textChange=(e)=>{
        setText(e.target.value)
    }
    const LeaveMessage=(text)=>{
        if(text!==''){
            axios.post('http://aays.top:4000/board/insert',qs.stringify({message:text})).then(()=>{
                alert('留言成功')
                updateData(()=>{setText('')})
            },
            err=>{
                alert('留言失败')
            }
            ) 
        }else{
            alert('输入不能为空')
        }
    }
    const Clicklove=(id)=>{
        if(loveBgc===''){
            setloveBgc('red')
            axios.get(`http://aays.top:4000/board/updatelove?id=${id}&option=increment`).then(()=>{
                updateData(()=>{})
            },err=>{
                alert('点赞失败');
            })
        }else{
            setloveBgc('')
            axios.get(`http://aays.top:4000/board/updatelove?id=${id}&option=decrement`).then(res=>{
                updateData(()=>{})
            },err=>{
                alert('取消点赞失败')
            })
        }
    }
   
    React.useEffect(()=>{
        axios.get('http://aays.top:4000/board/search').then(res=>{
            setMessageContent(res.data.reverse())
        },
        err=>{
            console.log('请求数据库失败',err);
        }
        )
    },[])
    return (
        <div className='message'>
            <div  className='message-input'>
          <TextArea rows={5}  onChange={textChange} value={text}/>
          <Button type="primary" onClick={()=>{LeaveMessage(text)}} className='search-btn' block>
      发表留言
    </Button>
          </div>
    
          <br />
          <br />
          <ul>
          {
              messageContent.map((item,index)=>{
                  return (
                  <li key={index}>
    <div className='message-content'>
   {item.message}
   <div className='message-love'><span onClick={()=>{Clicklove(item.id)}}><HeartTwoTone twoToneColor="#eb2f96" style={{backgroundColor:loveBgc}}/></span> {item.love}</div>
</div>
                  </li>
                  )
              })
          }
      </ul>
        </div>
    )
}
